import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Filters from './finder__filters';
import Summary from '../results/finder__results__summary';
import createFocusTrap from 'focus-trap';
// seems to fix most issues with old iPhones and position: fixed
// may not be necessary long-term, but I was in a hurry
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

/**
 * Render the mobile version of the filters.
 *
 * TODO: this is a bit flaky on old iPhones with long facets (i.e. longer than
 * the main body of the page)
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__mobilefilters(props) {
    const [display, setDisplay] = useState(false);
    const [focusTrap, setFocusTrap] = useState({});

    let filters = null;
    const getFilters = () => filters;

    // trap tab focus when the filters are open
    useEffect(() => {
        if (!focusTrap.activate) {
            setFocusTrap(
                createFocusTrap(getFilters(), {
                    onDeactivate: () => setDisplay(false),
                    clickOutsideDeactivates: true,
                })
            );
        }

        if (display) {
            focusTrap.activate && focusTrap.activate();
            disableBodyScroll(getFilters());
        } else {
            focusTrap.deactivate && focusTrap.deactivate();
            enableBodyScroll(getFilters());
        }
    }, [display]);

    const totalMatching =
        props.response &&
        props.response.summary &&
        props.response.summary.totalMatching;

    const result =
        totalMatching === 1
            ? props.summariseAs.singular
            : props.summariseAs.plural;

    const totalMatchingMessage = totalMatching
        ? `Show ${totalMatching} ${result}`
        : 'Close';

    const summary =
        totalMatching > 0 ? (
            <Summary
                currEnd={props.response.summary.currEnd}
                currStart={props.response.summary.currStart}
                query={props.query}
                numRanks={props.response.summary.numRanks}
                summariseAs={props.summariseAs}
                totalMatching={props.response.summary.totalMatching}
            />
        ) : (
            <div className="finder__results__summary">
                <h2 className="finder__results__summary__heading-wrap">
                    No {props.summariseAs.plural}
                </h2>
            </div>
        );

    const filtersCount =
        Object.keys(props.query.facets).length > 0 ? (
            <span>
                Filters{' '}
                <span className="finder__mobilefilters__toggle__filterNo">
                    ({Object.keys(props.query.facets).length})
                </span>
            </span>
        ) : (
            <span>Filter</span>
        );

    return (
        <div className="finder__filters--mobile" data-open={display}>
            <button
                type="button"
                className="finder__filters--mobile__toggle"
                aria-haspopup={true}
                aria-expanded={display}
                onClick={() => setDisplay(!display)}
                data-filters-applied={
                    Object.keys(props.query.facets).length !== 0 ? true : false
                }
            >
                <span>
                    <span
                        className="far fa-sliders-h icon"
                        aria-hidden="true"
                    />{' '}
                    {filtersCount}
                </span>
            </button>
            <div
                className="finder__filters--mobile__filters"
                ref={mobilefilters => {
                    filters = mobilefilters;
                }}
            >
                <div className="finder__filters--mobile__filters__content">
                    <Filters
                        config={props.config}
                        query={props.query}
                        response={props.response}
                        update={props.update}
                        clear={props.clear}
                        mobile={true}
                    />
                    <button
                        type="button"
                        className="finder__filters--mobile__apply"
                        aria-expanded={display}
                        onClick={() => setDisplay(!display)}
                        disabled={props.updating}
                    >
                        {Object.keys(props.query.facets).length === 0 ? null : (
                            <span className="far fa-chevron-left"></span>
                        )}

                        <span aria-live="polite">
                            {props.updating ? (
                                <React.Fragment>
                                    <span
                                        className="fas fa-spinner fa-pulse icon"
                                        aria-hidden="true"
                                    />{' '}
                                    <span>
                                        Updating {props.summariseAs.plural}
                                    </span>
                                </React.Fragment>
                            ) : (
                                <React.Fragment>
                                    <span>
                                        {Object.keys(props.query.facets)
                                            .length === 0
                                            ? 'Close'
                                            : totalMatchingMessage}
                                    </span>
                                </React.Fragment>
                            )}
                        </span>
                    </button>
                </div>
            </div>
            {summary}
        </div>
    );
}

finder__mobilefilters.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    response: PropTypes.object,
    update: PropTypes.object,
    clear: PropTypes.func,
    updating: PropTypes.bool,
    summariseAs: PropTypes.object,
};

export default finder__mobilefilters;
